.post {
  width: 100%;
  &__image_container {
    width: 100%;
    height: 500px;
    overflow: hidden;
    margin-bottom: 1rem;
  }
  &__image {
    width: 100%;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
  }
  &__title {
    font-size: 1.5rem;
    font-weight: bold;
    margin-bottom: 1rem;
  }
  &__description {
    font-size: 1rem;
    margin-bottom: 1rem;
  }
  &__footer {
  }
}
