@import "../../styles/variables.scss";

.header {
  background: $dark;
  width: 100%;
  height: $header-height;
  padding: 0 1.5rem;
  @include breakpoint(md) {
    padding: 0 3rem;
  }
  transition: 0.3s ease all;

  &__content {
    // overflow: hidden;
    color: $light;
    margin: 0 auto;
    max-width: 1920px;
    height: 100%;

    display: flex;
    align-items: center;
    justify-content: space-between;
    position: relative;
    z-index: 100;
    &__logo {
      //   font-size: 2rem;
      //   font-weight: bold;
      //   text-decoration: none;
      //   color: $light;

      width: 30px;
      height: 30px;
      background-size: cover;
      background-position: center;
      background-repeat: no-repeat;
    }
    &__login {
      padding: 0.75rem 1.25rem;
      border-radius: 12px;
      transition: 0.3s ease all;
      &__name {
        display: none;
        @include breakpoint(md) {
          display: contents;
        }
      }
      &__icon {
        font-size: 2rem;
        @include breakpoint(md) {
          font-size: 1.5rem;
        }
      }
    }
    &__nav {
      top: 0;
      right: 100%;
      bottom: 0;
      width: 100%;
      height: 100vh;
      position: fixed;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      text-align: center;
      background: rgba($dark, 0.9);
      backdrop-filter: blur(10px);
      transform: translate(0);
      transition: 0.3s ease transform;

      @include breakpoint(md) {
        transform: none;
        flex-direction: row;
        background: transparent;
        width: auto;
        height: 100%;
        position: static;
      }
      ul {
        list-style: none;

        display: flex;
        flex-direction: column;
        margin-bottom: $spacing-lg;
        @include breakpoint(md) {
          flex-direction: row;
          align-items: center;
          margin-bottom: 0;
          margin-right: calc(0.5rem + #{$spacing-md});
        }
        li {
          &:not(:last-child) {
            margin-bottom: $spacing-lg;
            @include breakpoint(md) {
              margin-bottom: 0;
              margin-right: $spacing-md;
            }
          }
          a {
            text-decoration: none;
            color: inherit;
            padding: 0.75rem 1.25rem;
            border-radius: $border-radius;
            transition: 0.3s ease all;

            &:hover {
              background: rgba($yellow, 0.1);
            }
            &:active {
              border-radius: calc(#{$border-radius} + 6px);
              background: linear-gradient(rgba($light, 0.1), rgba($light, 0.2));
            }
          }
        }
      }
    }
    // button {
    //   cursor: pointer;
    //   outline: none;
    //   padding: 0.75rem 1.25rem;
    //   border-radius: $border-radius;
    //   font-size: 1rem;
    //   font-family: inherit;
    //   color: $dark;
    //   border: 1px solid transparent;
    //   transition: 0.3s ease all;
    //   &:hover {
    //     border-color: $orange;
    //     background: rgba($color: $orange, $alpha: 0.1);
    //     color: $orange;
    //   }
    //   &:active {
    //     border-color: $orange;
    //     background: linear-gradient(rgba($orange, 0.1), rgba($orange, 0.2));
    //     color: $orange;
    //   }
    // }
    &__toggle {
      cursor: pointer;
      display: flex;
      justify-content: center;
      align-items: center;
      font-size: 2rem;
      transition: 0.3s ease all;
      position: relative;

      &:hover {
        color: $orange;
      }
      @include breakpoint(md) {
        display: none;
      }
    }
  }
}
.isMenu {
  transform: translate(100%);
}
